import {
  Box,
  Card,
  CardActionArea,
  Grid,
  Hidden,
  makeStyles,
  Typography,
} from "@material-ui/core"
import { blue } from "@material-ui/core/colors"
import blueGrey from "@material-ui/core/colors/blueGrey"
import lightBlue from "@material-ui/core/colors/lightBlue"
import { CSSProperties } from "@material-ui/core/styles/withStyles"
import ArrowBackIcon from "@material-ui/icons/ArrowBack"
import ArrowForward from "@material-ui/icons/ArrowForward"
import { graphql, navigate, PageProps } from "gatsby"
import React from "react"
import { BlogPostBySlugQuery } from "../../../graphql-types"
import Layout from "../../components/Layout"
import SEO from "../../components/seo"
import TableOfContents from "../../components/TableOfContents"
import "./prism.scss"
import "./style.scss"

const BlogPostTemplate: React.FC<PageProps<BlogPostBySlugQuery>> = ({
  data,
  location,
  pageContext,
}) => {
  const classes = useStyles()
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const { previous, next } = data

  return (
    <Layout siteTitle={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <Grid container>
        <Grid item xs={12} md={12} lg={8}>
          <Box>
            <Typography variant="h1" className={classes.title}>
              {post.frontmatter.title}
            </Typography>
            <Typography className={classes.date} variant="subtitle2">
              {post.frontmatter.date}
            </Typography>
            <div
              className={`${classes.blogContent} blog-contents`}
              dangerouslySetInnerHTML={{ __html: post.html }}
            />
          </Box>
          <Box>
            <Box className={classes.prevNextButtonContainer}>
              <Box>
                {previous && (
                  <Card variant="outlined" className={classes.prevNextCard}>
                    <CardActionArea
                      className={classes.prevNextCardArea}
                      onClick={() =>
                        setTimeout(() => navigate(previous.fields.slug), 300)
                      }
                    >
                      <Box className={classes.prevArrowContainer}>
                        <ArrowBackIcon className="prevNextIcon" />
                      </Box>
                      <Box className={classes.prevAllTextContainer}>
                        <Box className={classes.prevTextContainer}>
                          <Typography
                            variant="caption"
                            className={classes.prevNextText}
                          >
                            Previous
                          </Typography>
                        </Box>
                        <Box className={classes.prevTitleContainer}>
                          <Typography
                            className={`${classes.prevNextTitleText} prevNextTitleText`}
                          >
                            {previous.frontmatter.title}
                          </Typography>
                        </Box>
                      </Box>
                    </CardActionArea>
                  </Card>
                )}
              </Box>
              <Box>
                {next && (
                  <Card variant="outlined" className={classes.prevNextCard}>
                    <CardActionArea
                      className={classes.prevNextCardArea}
                      onClick={() =>
                        setTimeout(() => navigate(next.fields.slug), 300)
                      }
                    >
                      <Box className={classes.prevAllTextContainer}>
                        <Box className={classes.nextTextContainer}>
                          <Typography
                            variant="caption"
                            className={classes.prevNextText}
                          >
                            Next
                          </Typography>
                        </Box>
                        <Box className={classes.nextTitleContainer}>
                          <Typography
                            className={`${classes.prevNextTitleText} prevNextTitleText`}
                          >
                            {next.frontmatter.title}
                          </Typography>
                        </Box>
                      </Box>
                      <Box className={classes.nextArrowContainer}>
                        <ArrowForward className="prevNextIcon" />
                      </Box>
                    </CardActionArea>
                  </Card>
                )}
              </Box>
            </Box>
          </Box>
        </Grid>
        <Hidden mdDown>
          <Grid item xs={12} md={12} lg={4}>
            <TableOfContents
              content={data.markdownRemark.tableOfContents}
              className={classes.tableOfContents}
            />
          </Grid>
        </Hidden>
      </Grid>
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
      }
      tableOfContents(maxDepth: 3)
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
const useStyles = makeStyles(theme => {
  const headings: { [key: string]: CSSProperties } = {}
  for (let i = 1; i <= 6; i++) {
    headings[`& h${i}`] = {
      ...theme.typography[`h${i}`],
      margin: theme.spacing(6, 0, 3, 0),
      //fontSize: `${parseInt(theme.typography[`h${i}`].fontSize) - 4}px`,
    }
  }

  return {
    prevNextButtonContainer: {
      display: "grid",
      columnGap: "24px",
      rowGap: "12px",
      gridTemplateRows: "auto",
      gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
      margin: theme.spacing(0, 0, 4, 0),
    },
    prevArrowContainer: {
      flex: "0",
      padding: theme.spacing(2, 0, 2, 2),
      textAlign: "center",
      color: blueGrey[300],
    },
    nextArrowContainer: {
      flex: "0",
      padding: theme.spacing(2, 2, 2, 0),
      textAlign: "center",
      color: blueGrey[300],
    },
    prevNextCardArea: {
      display: "flex",
      flexDirection: "row",
    },
    prevAllTextContainer: {
      flex: "1",
      padding: theme.spacing(0, 2),
    },
    prevTextContainer: {
      textAlign: "right",
    },
    prevTitleContainer: {
      textAlign: "right",
    },
    nextTextContainer: {
      textAlign: "left",
    },
    nextTitleContainer: {
      textAlign: "left",
    },
    prevNextTitleText: {
      fontSize: "16px",
      fontWeight: "bold",
      color: theme.typography.h6.color,
      display: "-webkit-box",
      overflow: "hidden",
      textOverflow: "ellipsis",
      "-webkit-line-clamp": 1,
      "-webkit-box-orient": "vertical",
    },
    prevNextCard: {
      height: "80px",
      borderRadius: "3px",
      boxShadow: "rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;",
      transition: "border 250ms ease 0s",
      display: "flex",
      "&:hover": {
        borderColor: lightBlue[800],
        "& .prevNextIcon, .prevNextTitleText": {
          color: lightBlue[800],
          transition: "color 250ms ease 0s",
        },
      },
    },
    prevNextText: {
      color: blueGrey[300],
    },
    title: {
      margin: theme.spacing(4, 0, 0, 0),
      [theme.breakpoints.down("xs")]: {
        fontSize: "30px",
      },
    },
    date: {
      margin: theme.spacing(0, 0, 4, 0),
      color: blueGrey[500],
    },
    tableOfContents: {
      margin: theme.spacing(0, 0, 4, 0),
    },
    blogContent: {
      ...headings,
      "& figcaption": {
        "& p": {
          "&::before": {
            content: '"[ "',
          },
          "&::after": {
            content: '" ]"',
          },
          textAlign: "center",
          color: theme.typography.caption.color,
          fontSize: theme.typography.caption.fontSize,
        },
      },
    },
  }
})
